.notifications-page {
  .notification-item {
    gap: 2rem;
    .ant-list-item-meta {
      .ant-list-item-meta-content {
        h4 {
          word-break: break-word;
        }
      }
    }
    span {
      font-size: 2rem;
      cursor: pointer;
      transition: 0.2s all linear;
      &:hover {
        color: #4096ff;
      }
    }
  }

  .notification-with-link {
    .ant-list-item-meta {
      .ant-list-item-meta-content {
        h4 {
          word-break: break-word;
          color: #1677ff !important;
          cursor: pointer;
        }
      }
    }
  }

  .ant-pagination-options {
    display: none;
  }
}

@media screen and (max-width: 626px) {
  .notifications-page {
    .notification-item {
      .ant-list-item-meta {
        .ant-list-item-meta-content {
          h4 {
            font-size: 1.4rem;
          }
        }
        .ant-list-item-meta-description {
          font-size: 1.2rem;
        }
      }
    }
    .notification-with-link {
      .ant-list-item-meta {
        .ant-list-item-meta-content {
          h4 {
            font-size: 1.4rem;
          }
        }
        .ant-list-item-meta-description {
          font-size: 1.2rem;
        }
      }
    }
  }
}
